/**
 * @description patch textarea view
 * @author wangfupeng
 */

import { h, VNode } from 'snabbdom'
import { IDomEditor } from '../editor/interface'
import TextArea from './TextArea'
import { genPatchFn, normalizeVnodeData } from '../utils/vdom'
import $, { Dom7Array, getDefaultView, getElementById } from '../utils/dom'
import { node2Vnode } from '../render/node2Vnode'
import {
  IS_FIRST_PATCH,
  TEXTAREA_TO_PATCH_FN,
  TEXTAREA_TO_VNODE,
  EDITOR_TO_ELEMENT,
  NODE_TO_ELEMENT,
  ELEMENT_TO_NODE,
  EDITOR_TO_WINDOW,
} from '../utils/weak-maps'

function genElemId(id: number) {
  return `w-e-textarea-${id}`
}

/**
 * çæç¼è¾åºåèç¹ç vnode
 * @param elemId elemId
 * @param readOnly readOnly
 */
function genRootVnode(elemId: string, readOnly = false): VNode {
  return h(`div#${elemId}`, {
    props: {
      contentEditable: readOnly ? false : true,
    },
  })
  // å¶ä»å±æ§å¨ genRootElem ä¸­å®ï¼è¿éä¸ç¨éå¤å
}

/**
 * çæç¼è¾åºåç elem
 * @param elemId elemId
 * @param readOnly readOnly
 */
function genRootElem(elemId: string, readOnly = false): Dom7Array {
  const $elem = $(`<div
        id="${elemId}"
        data-slate-editor
        data-slate-node="value"
        suppressContentEditableWarning
        role="textarea"
        spellCheck="true"
        autoCorrect="true"
        autoCapitalize="true"
    ></div>`)

  // role="textarea" - å¢å¼ºè¯­ä¹ï¼div è¯­ä¹å¤ªå¼±

  return $elem
}

/**
 * è·å editor.children æ¸²æ DOM
 * @param textarea textarea
 * @param editor editor
 */
function updateView(textarea: TextArea, editor: IDomEditor) {
  const $scroll = textarea.$scroll
  const elemId = genElemId(textarea.id)
  const { readOnly, autoFocus } = editor.getConfig()

  // çæ newVnode
  const newVnode = genRootVnode(elemId, readOnly)
  const content = editor.children || []
  newVnode.children = content.map((node, i) => {
    let vnode = node2Vnode(node, i, editor, editor)
    normalizeVnodeData(vnode) // æ´ç vnode.data ä»¥ç¬¦å snabbdom çè¦æ±
    return vnode
  })

  let textareaElem
  let isFirstPatch = IS_FIRST_PATCH.get(textarea)
  if (isFirstPatch == null) isFirstPatch = true // å°æªèµå¼ï¼ä¹æ¯ç¬¬ä¸æ¬¡
  if (isFirstPatch) {
    // ç¬¬ä¸æ¬¡ patch ï¼åçæ elem
    const $textArea = genRootElem(elemId, readOnly)
    $scroll.append($textArea)
    textarea.$textArea = $textArea // å­å¨ä¸ç¼è¾åºåç DOM èç¹
    textareaElem = $textArea[0]

    // åçæ patch å½æ°ï¼å¹¶æ§è¡
    const patchFn = genPatchFn()
    patchFn(textareaElem, newVnode)

    // å­å¨ç¸å³ä¿¡æ¯
    IS_FIRST_PATCH.set(textarea, false) // ä¸åæ¯ç¬¬ä¸æ¬¡ patch
    TEXTAREA_TO_PATCH_FN.set(textarea, patchFn) // å­å¨ patch å½æ°
  } else {
    // ä¸æ¯ç¬¬ä¸æ¬¡ patch
    const curVnode = TEXTAREA_TO_VNODE.get(textarea)
    const patchFn = TEXTAREA_TO_PATCH_FN.get(textarea)
    if (curVnode == null || patchFn == null) return
    textareaElem = curVnode.elm

    patchFn(curVnode, newVnode)
  }

  if (textareaElem == null) {
    textareaElem = getElementById(elemId)

    // éè¿ getElementById è·åçæå¯è½æ¯ null ï¼éæ¯ãéå»ºæ¶ï¼å¯è½ä¼åçè¿ç§æåµï¼
    if (textareaElem == null) return
  }

  // focus
  let isFocused
  if (isFirstPatch) {
    // åæ¬¡æ¸²æ
    isFocused = autoFocus
  } else {
    // éåæ¬¡æ¸²æ
    isFocused = editor.isFocused()
  }
  if (isFocused) {
    textareaElem.focus({
      preventScroll: true, // å¿é¡»æ·»å  preventScroll éé¡¹ï¼å¦åå¼¹çªæèç¼è¾å¨å¤±ç¦ä¼å¯¼è´ç¼è¾åºåèªå¨æ»å¨å°é¡¶é¨
    })
  }

  // å­å¨ç¸å³ä¿¡æ¯
  if (isFirstPatch) {
    const window = getDefaultView(textareaElem)
    window && EDITOR_TO_WINDOW.set(editor, window)
  }

  EDITOR_TO_ELEMENT.set(editor, textareaElem) // å­å¨ editor -> elem å¯¹åºå³ç³»
  NODE_TO_ELEMENT.set(editor, textareaElem)
  ELEMENT_TO_NODE.set(textareaElem, editor)
  TEXTAREA_TO_VNODE.set(textarea, newVnode) // å­å¨ vnode
}

export default updateView
