import { Editor } from 'slate'
import { IDomEditor } from '../../editor/interface'
import TextArea from '../TextArea'
import { HAS_BEFORE_INPUT_SUPPORT } from '../../utils/ua'
import { hasEditableTarget } from '../helpers'

// ãæ³¨æãè½ç¶ keypress äºä»¶å·²ç»è¿æ¶ï¼å»ºè®®ç¨ keydown åä»£ï¼ï¼ä½è¿éæ¯ä¸ºäºå¼å®¹ beforeinput ï¼æä»¥ä¸ä¼å¨é«çº§æµè§å¨çæï¼ä¸ç¨åçº§ keydown

function handleKeypress(event: Event, textarea: TextArea, editor: IDomEditor) {
  // è¿éæ¯å¼å®¹ä¸å®å¨æ¯æ beforeInput çæµè§å¨ãå¯¹äºæ¯æ beforeInput çæµè§å¨ï¼ä¼ç¨ beforeinput äºä»¶å¤ç
  if (HAS_BEFORE_INPUT_SUPPORT) return

  const { readOnly } = editor.getConfig()
  if (readOnly) return
  if (!hasEditableTarget(editor, event.target)) return

  event.preventDefault()

  const text = (event as any).key as string

  // è¿éåªå¼å®¹ beforeInput ç insertText ç±»åï¼å¶ä»çï¼å¦å é¤ãæ¢è¡ï¼ä½¿ç¨ keydown æ¥å¼å®¹
  Editor.insertText(editor, text)
}

export default handleKeypress
