import { Editor, Node, Text, Path, Operation, Range, Transforms, Element, Descendant } from 'slate'
import { DomEditor } from '../dom-editor'
import { IDomEditor } from '../..'
import { EDITOR_TO_SELECTION, NODE_TO_KEY } from '../../utils/weak-maps'
import node2html from '../../to-html/node2html'
import { genElemId } from '../../render/helper'
import { Key } from '../../utils/key'
import $, { DOMElement, getPlainText, getTagName, NodeType } from '../../utils/dom'
import { findCurrentLineRange } from '../../utils/line'
import { ElementWithId } from '../interface'
import { PARSE_ELEM_HTML_CONF, TEXT_TAGS } from '../../parse-html/index'
import parseElemHtml from '../../parse-html/parse-elem-html'

const IGNORE_TAGS = new Set([
  'doctype',
  '!doctype',
  'meta',
  'script',
  'style',
  'link',
  'frame',
  'iframe',
  'title',
  'svg', // TODO ææ¶å¿½ç¥
])

export const withContent = <T extends Editor>(editor: T) => {
  const e = editor as T & IDomEditor
  const { onChange, insertText, apply, deleteBackward } = e

  // éå apply æ¹æ³
  // apply æ¹æ³éå¸¸éè¦ï¼å®æç»æ§è¡ operation https://docs.slatejs.org/concepts/05-operations
  // operation çæ¥å£å®ä¹åè slate src/interfaces/operation.ts
  e.apply = (op: Operation) => {
    const matches: [Path, Key][] = []

    const { readOnly } = e.getConfig()
    if (readOnly) return

    switch (op.type) {
      case 'insert_text':
      case 'remove_text':
      case 'set_node': {
        for (const [node, path] of Editor.levels(e, { at: op.path })) {
          // å¨å½åèç¹å¯»æ¾
          const key = DomEditor.findKey(e, node)
          matches.push([path, key])
        }
        break
      }

      case 'insert_node':
      case 'remove_node':
      case 'merge_node':
      case 'split_node': {
        for (const [node, path] of Editor.levels(e, { at: Path.parent(op.path) })) {
          // å¨ç¶èç¹å¯»æ¾
          const key = DomEditor.findKey(e, node)
          matches.push([path, key])
        }
        break
      }

      case 'move_node': {
        for (const [node, path] of Editor.levels(e, {
          at: Path.common(Path.parent(op.path), Path.parent(op.newPath)),
        })) {
          const key = DomEditor.findKey(e, node)
          matches.push([path, key])
        }
        break
      }
    }

    // æ§è¡åæ¬ç apply - éè¦ï¼ï¼ï¼
    apply(op)

    // ç»å® node å key
    for (const [path, key] of matches) {
      const [node] = Editor.node(e, path)
      NODE_TO_KEY.set(node, key)
    }
  }

  e.deleteBackward = unit => {
    if (unit !== 'line') {
      return deleteBackward(unit)
    }

    if (editor.selection && Range.isCollapsed(editor.selection)) {
      const parentBlockEntry = Editor.above(editor, {
        match: n => Editor.isBlock(editor, n),
        at: editor.selection,
      })

      if (parentBlockEntry) {
        const [, parentBlockPath] = parentBlockEntry
        const parentElementRange = Editor.range(editor, parentBlockPath, editor.selection.anchor)

        const currentLineRange = findCurrentLineRange(e, parentElementRange)

        if (!Range.isCollapsed(currentLineRange)) {
          Transforms.delete(editor, { at: currentLineRange })
        }
      }
    }
  }

  // éå onchange API
  e.onChange = () => {
    // è®°å½å½åéåº
    const { selection } = e
    if (selection != null) {
      EDITOR_TO_SELECTION.set(e, selection)
    }

    // è§¦åéç½®ç change äºä»¶
    e.emit('change')

    onChange()
  }

  // tab
  e.handleTab = () => {
    e.insertText('    ')
  }

  // è·å html ï¼å»æäºæ ¼å¼å 2021.12.10ï¼
  e.getHtml = (): string => {
    const { children = [] } = e
    const html = children.map(child => node2html(child, e)).join('')
    return html
  }

  // è·å text
  e.getText = (): string => {
    const { children = [] } = e
    return children.map(child => Node.string(child)).join('\n')
  }

  // è·åéåºæå­
  e.getSelectionText = (): string => {
    const { selection } = e
    if (selection == null) return ''
    return Editor.string(editor, selection)
  }

  // æ ¹æ® type è·å elems
  e.getElemsByType = (type: string, isPrefix = false): ElementWithId[] => {
    const elems: ElementWithId[] = []

    // è·å editor ææ nodes
    const nodeEntries = Editor.nodes(e, {
      at: [],
      universal: true,
    })
    for (let nodeEntry of nodeEntries) {
      const [node] = nodeEntry
      if (Element.isElement(node)) {
        // å¤æ­ type ï¼åç¼ or å¨ç­ï¼
        let flag = isPrefix ? node.type.indexOf(type) >= 0 : node.type === type
        if (flag) {
          const key = DomEditor.findKey(e, node)
          const id = genElemId(key.id)

          // node + id
          elems.push({
            ...node,
            id,
          })
        }
      }
    }

    return elems
  }

  // æ ¹æ® type åç¼ï¼è·å elems
  e.getElemsByTypePrefix = (typePrefix: string): ElementWithId[] => {
    return e.getElemsByType(typePrefix, true)
  }

  /**
   * å¤æ­ editor æ¯å¦ä¸ºç©ºï¼åªæä¸ä¸ªç©º paragraphï¼
   */
  e.isEmpty = () => {
    const { children = [] } = e
    if (children.length > 1) return false // >1 ä¸ªé¡¶çº§èç¹

    const firstNode = children[0]
    if (firstNode == null) return true // editor.children ç©ºæ°ç»

    if (Element.isElement(firstNode) && firstNode.type === 'paragraph') {
      const { children: texts = [] } = firstNode
      if (texts.length > 1) return false // >1 text node

      const t = texts[0]
      if (t == null) return true // æ  text èç¹

      if (Text.isText(t) && t.text === '') return true // åªæä¸ä¸ª text ä¸æ¯ç©ºå­ç¬¦ä¸²
    }

    return false
  }

  /**
   * æ¸ç©ºåå®¹
   */
  e.clear = () => {
    const initialEditorValue: Node[] = [
      {
        type: 'paragraph',
        children: [{ text: '' }],
      },
    ]
    Transforms.removeNodes(e, {
      at: {
        anchor: Editor.start(e, []),
        focus: Editor.end(e, []),
      },
      mode: 'highest',
    })

    if (e.children.length === 0) {
      Transforms.insertNodes(e, initialEditorValue)
    }
  }

  e.getParentNode = (node: Node) => {
    return DomEditor.getParentNode(e, node)
  }

  /**
   * æå¥ html ï¼ä¸ä¿è¯è¯­ä¹å®å¨æ­£ç¡®ï¼ï¼ç¨äºç²è´´
   * @param html html string
   */
  e.dangerouslyInsertHtml = (html: string = '') => {
    if (!html) return

    // ------------- æ html è½¬æ¢ä¸º $elems -------------
    let $elems = $(html)
    $elems = $elems.filter(el => {
      if (el.nodeType !== NodeType.ELEMENT_NODE) return false

      const $el = $(el)
      const tagName = getTagName($el)
      if (IGNORE_TAGS.has(tagName)) return false // è¿æ»¤æå¿½ç¥ç tag

      return true
    })
    if ($elems.length === 0) return

    // ------------- æ $elems è½¬æ¢ä¸º nodes -------------
    const $div = $('<div hidden="true"></div>')
    $div.append($elems)
    $('body').append($div) // æ·»å å° body ï¼ä»¥ä¾¿è·å style ï¼å¤æ­æå­æ¢è¡

    const nodes: Descendant[] = []
    $elems.forEach(el => {
      // å¤æ­å½åç el æ¯å¦æ¯å¯è¯å«ç tag
      let isParseMatch = false
      if (TEXT_TAGS.includes(el.tagName.toLowerCase())) {
        // text elemï¼å¦ <span>
        isParseMatch = true
      } else {
        for (let selector in PARSE_ELEM_HTML_CONF) {
          if (el.matches(selector)) {
            // æ®é elemï¼å¦ <p> <a> ç­ï¼é text elemï¼
            isParseMatch = true
            break
          }
        }
      }

      // å¹éä¸äºï¼åçæ slate elem
      if (isParseMatch) {
        const $el = $(el)
        nodes.push(parseElemHtml($el, e))
        return
      }

      // æ²¡æå¹éä¸ï¼åæå¥çº¯ææ¬
      const text = getPlainText(el).trim()
      if (!text) return
      const textLines = text.split(/\r\n|\r|\n/) // æ¢è¡
      textLines.forEach((line, index) => {
        if (!line.trim()) return
        nodes.push({ type: 'paragraph', children: [{ text: line }] }) // æ²¡è¡é½çæä¸ä¸ª paragraph
      })
    })

    // ------------- å° nodes æå¥å°ç¼è¾å¨ -------------
    if (nodes.length) {
      e.insertFragment(nodes)
    }
    $div.remove()
  }

  return e
}
