import $, { Dom7Array } from '../../utils/dom'
import { IBarItem, getEditorInstance } from './index'
import { IOption, ISelectMenu } from '../interface'
import SelectList from '../panel-and-modal/SelectList'
import { gen$downArrow } from '../helpers/helpers'
import { promiseResolveThen } from '../../utils/util'
import { addTooltip } from './tooltip'

// æ ¹æ® option value è·å text
function getOptionText(options: IOption[], value: string): string {
  const length = options.length
  let text = ''
  for (let i = 0; i < length; i++) {
    const opt = options[i]
    if (opt.value === value) {
      text = opt.text
      break
    }
  }
  return text
}

class BarItemSelect implements IBarItem {
  readonly $elem: Dom7Array = $(`<div class="w-e-bar-item"></div>`)
  private readonly $button: Dom7Array = $(`<button type="button" class="select-button"></button>`)
  menu: ISelectMenu
  private disabled = false
  private selectList: SelectList | null = null

  constructor(menu: ISelectMenu, inGroup = false) {
    // éªè¯ tag
    const { tag, title, width, iconSvg = '', hotkey = '' } = menu
    if (tag !== 'select') throw new Error(`Invalid tag '${tag}', expected 'select'`)

    // åå§å dom
    const $button = this.$button
    if (width) {
      $button.css('width', `${width}px`)
    }
    addTooltip($button, iconSvg, title, hotkey, inGroup) // è®¾ç½® tooltip
    this.$elem.append($button)

    this.menu = menu

    // å¼æ­¥ç»å®äºä»¶
    promiseResolveThen(() => this.init())
  }

  private init() {
    // è®¾ç½® select å±æ§
    this.setSelectedValue()

    // select button click
    this.$button.on('mousedown', (e: Event) => {
      e.preventDefault()
      const editor = getEditorInstance(this)
      editor.hidePanelOrModal() // éèå½åçåç§ panel
      this.trigger()
    })
  }

  private trigger() {
    const editor = getEditorInstance(this)

    if (editor.isDisabled()) return
    if (this.disabled) return

    const menu = this.menu

    // æ¾ç¤ºä¸æåè¡¨
    if (this.selectList == null) {
      // åæ¬¡åå»ºï¼æ¸²æ list å¹¶æ¾ç¤º
      this.selectList = new SelectList(editor, menu.selectPanelWidth)
      const selectList = this.selectList
      const options = menu.getOptions(editor)
      selectList.renderList(options)
      selectList.appendTo(this.$elem)
      selectList.show()

      // åæ¬¡åå»ºï¼ç»å®äºä»¶
      selectList.$elem.on('mousedown', 'li', (e: Event) => {
        const { target } = e
        if (target == null) return

        e.preventDefault()
        const $li = $(target)
        const val = $li.attr('data-value')
        this.onChange(val)
      })
    } else {
      // ä¸æ¯åæ¬¡åå»º
      const selectList = this.selectList
      if (selectList.isShow) {
        // å½åå¤äºæ¾ç¤ºç¶æï¼åéè
        selectList.hide()
      } else {
        // å½åæªå¤äºæ¾ç¤ºç¶æï¼åéæ°æ¸²æ list ï¼å¹¶æ¾ç¤º
        const options = menu.getOptions(editor) // æ¯æ¬¡é½è¦éæ°è·å options ï¼å ä¸ºéä¸­é¡¹å¯è½ä¼åå
        selectList.renderList(options)
        selectList.show()
      }
    }
  }

  private onChange(value: string) {
    const editor = getEditorInstance(this)
    const menu = this.menu
    menu.exec && menu.exec(editor, value)
  }

  private setSelectedValue() {
    const editor = getEditorInstance(this)
    const menu = this.menu
    const value = menu.getValue(editor)

    const options = menu.getOptions(editor)
    const optText = getOptionText(options, value.toString())

    const $button = this.$button
    const $downArrow = gen$downArrow() // åä¸çç®­å¤´å¾æ 
    $button.html('')
    $button.text(optText)
    $button.append($downArrow)
  }

  private setDisabled() {
    const editor = getEditorInstance(this)
    const menu = this.menu
    let disabled = menu.isDisabled(editor)
    const $button = this.$button

    if (editor.selection == null || editor.isDisabled()) {
      // æªéä¸­ï¼æè readOnly ï¼å¼ºè¡è®¾ç½®ä¸º disabled
      disabled = true
    }

    const className = 'disabled'
    if (disabled) {
      // è®¾ç½®ä¸º disabled
      $button.addClass(className)
    } else {
      // åæ¶ disabled
      $button.removeClass(className)
    }

    this.disabled = disabled // è®°å½ä¸æ¥
  }

  changeMenuState() {
    this.setSelectedValue()
    this.setDisabled()
  }
}

export default BarItemSelect
