import { Dom7Array } from '../../utils/dom'
import { IS_APPLE } from '../../utils/ua'

export function addTooltip(
  $button: Dom7Array,
  iconSvg: string,
  title: string,
  hotkey: string,
  inGroup = false
) {
  if (!iconSvg) {
    // æ²¡æ icon ç´æ¥æ¾ç¤º title ï¼ä¸ç¨ tooltip
    return
  }

  if (hotkey) {
    const fnKey = IS_APPLE ? 'cmd' : 'ctrl' // mac OS è½¬æ¢ä¸º cmd ï¼windows è½¬æ¢ä¸º ctrl
    hotkey = hotkey.replace('mod', fnKey)
  }

  if (inGroup) {
    // in groupButton ï¼tooltip åªæ¾ç¤º å¿«æ·é®
    if (hotkey) {
      $button.attr('data-tooltip', hotkey)
      $button.addClass('w-e-menu-tooltip-v5')
      $button.addClass('tooltip-right') // tooltip æ¾ç¤ºå¨å³ä¾§
    }
  } else {
    // é in groupButton ï¼æ­£å¸¸å®ç° tooltip
    const tooltip = hotkey ? `${title}\n${hotkey}` : title
    $button.attr('data-tooltip', tooltip)
    $button.addClass('w-e-menu-tooltip-v5')
  }
}
